import React, { Component } from 'react';

class FairytaleQuestion extends Component {
    state = { 
        //Story fairytale:
        questions: [],
        qid: 0,
        suggestion: '',
        submision: '',
        submitToParent: null,
        choices: [],
    }
    constructor(props) {
        super(props);
        //Do it something!
        console.log('Constructor got: ' + props)
        console.log('Constructor got qid: ' + props.qid)
        //this.setState({submision: suggestion})
        this.state.qid = props.qid
        this.state.submitToParent = props.cb
        this.state.questions = props.q
        var choices = props.q[props.qid].choices.slice();
        this.state.choices = choices
        console.log('Constructor got callback: ' + props.cb)
        //Change suggestion to userChoices and make it an array:
        var userChoices = [];
        for(var i = 0; i < choices.length; i++) {
            userChoices.push('');
        }
        //TODO:Does its work or needs split?
        this.state.userChoices = userChoices;
    }
    //Generate responce for the choice id given:
    generateResponse = (cid) => {
        //Generate response ONLY for specific choice id:
        console.log('Choice ID given: ' + cid)
        var qid = this.state.qid
        var question = this.state.questions[qid].title
        var suggestions = this.state.questions[qid].suggestions
        var rnd_idx = Math.floor(Math.random() * suggestions.length) ;
        //Get random suggestion:
        var suggestion = suggestions[rnd_idx];
        var userChoices = this.state.userChoices;
        //Update only cid user choice:
        console.log(userChoices)
        console.log('Updating user choices '+userChoices.length+' from: '+userChoices)
        userChoices[cid] = suggestion
        console.log('...to: '+userChoices)
        //TODO: works or needs split?
        this.setState({submision: userChoices});
        console.log(question + ' Suggested: ' + suggestion);
        console.log(qid)
        //Submit to parent ALL the user choices:
        this.state.submitToParent(
            {
                //this changes from suggestion to userChoices
                submission: userChoices,
                qid: qid
            }
        )
    }
    submissionChange = (props) => {
        console.log(props);
        var updatedValue = props.event.target.value;
        var cid = props.key;
        //this.setState({submision: updatedValue});
        //TODO: fix copy paste from above
        var userChoices = this.state.userChoices;
        console.log('Updating user choices '+userChoices.length+' from: '+userChoices)
        userChoices[cid] = updatedValue
        console.log('...to: '+userChoices)
        //Update only cid user choice:
        console.log(userChoices)
        //TODO: works or needs split?
        this.setState({submision: userChoices});
        //End TODO
        console.log('Submission done: ' + updatedValue);
        this.state.submitToParent(
            {
                submission: userChoices,
                qid: this.state.qid
            }
        )
    }
    //We don't need that function after all!
    getSubmissionValue = (k) => {
        //pass
        var updatedValue = this.state.userChoices[k];
        console.log('getSubmissionValue: '+updatedValue)
        return updatedValue
    }
    createChoiceElement = (props) => {
        //blah
        var key = props.key
        var celement = <div className="btn-group-vertical">
            <input value={this.state.userChoices[key]} onChange={(e)=>this.submissionChange({event: e,key: key})} type="text" className="form-control" placeholder="Location" aria-label="Recipient's username" aria-describedby="button-addon2"/>
            <button onClick={()=>this.generateResponse(key)} type="button" className="btn btn-outline-primary btn-long">Generate</button>
        </div>;
        return celement
    }
    populateChoices = () => {
        //blah
        var i;
        var choices_array = []
        for (i=0; i<this.state.choices.length; i++){
            //console.log('Generating Choice '+i+1)
            choices_array.push(this.createChoiceElement({key: i}))
        }
        return choices_array
    }
    render() { 
        return ( 
            <div className="col-sm">
            <div className="alert alert-light" role="alert">
                <h5 className="alert-heading">{this.state.questions[this.state.qid].title}</h5>
                <hr/>
                {this.populateChoices()}
                <div>{this.state.choices.length}</div>
            </div>
            </div>
        );
    }
}
 
export default FairytaleQuestion;